import { logout } from "./service/user";


let socket: WebSocket | null = null;
let start: boolean | false = false;
let visitedUrls: Set<string> = new Set();
let accessToken: string | null = null;
let socketReconnect: boolean = true;       //  
let fileId: number = 0;

chrome.storage.onChanged.addListener((changes, namespace) => {
  if (namespace === 'local' && changes.accessToken) {
    accessToken = changes.accessToken.newValue;
  }
});

chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
  if (message.command === "start") {
    console.log("start   ")
    start = true;
    socketReconnect = true;

    if (!socket || socket.readyState === WebSocket.CLOSED) {
        connectWebSocket();
    }
    chrome.tabs.onUpdated.addListener(handleTabUpdate);
  } else if (message.command === "stop") {
    console.log("stop   ")
    socketReconnect = false
    visitedUrls.clear();
    //   
    if (socket) {
        socket.close();
        socket = null;
    }
    chrome.tabs.onUpdated.removeListener(handleTabUpdate);
  } else if (message.command === 'fileSelected') {
    fileId = message.fileId;
    chrome.storage.local.set({ selectedFileId: fileId }); 
    console.log("Selected file ID: ", fileId);
  } else if (message.command === 'logout') {
    logout()
  }
});

function getAccessToken(): Promise<string | null> {
  return new Promise((resolve, reject) => {
    chrome.storage.local.get(['accessToken'], (result) => {
      console.log("getAccessToken ")
      if (chrome.runtime.lastError) {
        console.error('Failed to retrieve access token:', chrome.runtime.lastError.message);
        resolve(null);
      } else {
        resolve(result.accessToken as string);
      }
    });
  });
}

async function connectWebSocket() {
  try {
    console.log("connectWebSocket ")

    if (!accessToken) {
      accessToken = await getAccessToken();
      if (!accessToken) {
        console.error("    ");
        return;
      }
    }

    if (socket !== null && socket.readyState !== WebSocket.CLOSED) {
      return; //        
    }

    socket = new WebSocket('wss://k10d209.p.ssafy.io/process/ws');
    // socket = new WebSocket('ws://localhost:18080/process/ws');

    socket.onopen = () => {
      console.log(" onopen ");

      const messageData = {
        sourceApplication: "CHROME",
        accessToken: accessToken,
        connectedFileId: fileId,
        dataType: "SIGNAL",
        content: "  onopen",
        timestamp: new Date()
      };

      const messageJson = JSON.stringify(messageData);
      if (socket) {
        socket.send(messageJson);
      } else {
        console.error("WebSocket connection is not established.");
      }
    };

    socket.onerror = (error: Event) => {
      console.error("WebSocket Error: ", error);
    };

    socket.onclose = async (e) => {
      console.log("WebSocket  .", e);
      if (socketReconnect) {
        console.log("1   .");
        setTimeout(() => {
          connectWebSocket();
        }, 1000);
      } else {
        console.log("     .");
      }
    };

  } catch (e) {
    console.error("WebSocket connection failed: ", e);
  }
}

async function handleTabUpdate(tabId: number, changeInfo: chrome.tabs.TabChangeInfo, tab: chrome.tabs.Tab) {
  console.log("handleTabUpdate ")

  if (!accessToken) {
      accessToken = await getAccessToken();
      if (!accessToken) {
        console.error("    ");
        return;
      }
    }
  if (start && tab.url && changeInfo.status === 'complete') {

    //     
    if (visitedUrls.has(tab.url)) {
      console.log("  URL: ", tab.url);
      return;
    }

    //  URL  
    visitedUrls.add(tab.url);
    console.log(" URL : ", tab.url);

    chrome.scripting.executeScript({
      target: { tabId },
      func: getHtml,
    }, (results) => {
      if (results) {
        // const pageHtml = results[0].result;
        let message = '';

        if (tab.url && tab.url.startsWith('https://www.google.com/search')) {
            const urlParams = new URLSearchParams(new URL(tab.url).search);
            const searchQuery = urlParams.get('q') || 'Unknown Search'; //  
            message = searchQuery
            
            const messageData = {
              sourceApplication: "CHROME",
              accessToken: accessToken,
              connectedFileId: fileId,
              dataType: "KEYWORD",
              content: message,
              timestamp: new Date()
            };
            const messageJson = JSON.stringify(messageData); //  JSON  
            console.log(" ?", socket)
    
            if (socket) { // socket.current null   send 
              console.log(" ~~")
              socket.send(messageJson); // JSON  
            } else {
              console.error("WebSocket connection is not established.");
            }
        } else if (tab.url && tab.url.startsWith('https://chatgpt.com/')) {
          console.log("gpt  ");
        } else if (tab.url) {
            const pageUrl = tab.url;
            console.log(pageUrl);
            if (pageUrl) {
                
              message = pageUrl;
            
              const messageData = {
                sourceApplication: "CHROME",
                accessToken: accessToken,
                connectedFileId: fileId,
                dataType: "SEARCH",
                content: message,
                timestamp: new Date()
              };
              const messageJson = JSON.stringify(messageData); //  JSON  
              console.log(" ?", socket)
      
              if (socket) { // socket.current null   send 
                console.log("url ~~")
                socket.send(messageJson); // JSON  
              } else {
                console.error("WebSocket connection is not established.");
              }
            } else {
              console.log("pageUrl null ~~")
            }
        } else {
          console.log("tab.url null ~~")
        }
      } else {
        console.log("results ")
      }
    });
  } else {
    if (!start) {
      console.log("start false ")
    } else if (changeInfo.status !== 'complete') {
      console.log("changeInfo complete ")
    }
  }
}

function getHtml(): string {
  return document.documentElement.outerHTML;
}